#run {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
#run #video-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
  position: relative;
}
#run #video-wrap button {
  position: absolute;
  left: 0;
  top: 0px;
  border: 0;
  outline: 0;
  padding: 10px 15px 10px 15px;
  border-radius: 25px;
  font-weight: bold;
  background: #fff;
  display: flex;
  gap: 10px;
  align-self: center;
  align-items: center;
  color: #004257;
  box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
  outline: 2px solid #fff;
  transition: outline 0.3s;
  cursor: pointer;
}
#run #video-wrap button:hover {
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  outline: 2px solid #004257;
}
#run #video-wrap #video {
  width: 100%;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 5px;
  margin: 0;
  padding: 0;
  display: inline-block;
  animation: 0.3s scaleIn forwards;
}
#run .loader {
  width: 48px;
  height: 48px;
  border: 5px solid #004257;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  position: fixed;
  left: calc(50% - 24px);
  top: calc(50% - 24px);
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes scaleIn {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}/*# sourceMappingURL=run.css.map */